.home-container {
  min-height: 100vh;
  background: linear-gradient(
    to bottom,
    #fff6e5,
    #ffe0b2,
    #ffcc80
  );
  color: #4a2c0a;
  font-family: "Georgia", "Times New Roman", serif;
  padding-bottom: 60px;
}

/* HERO */
.home-hero {
  text-align: center;
  padding: 80px 20px 40px;
}

.home-title {
  font-size: 3rem;
  margin-bottom: 12px;
}

.home-title span {
  color: #e65100; /* deep saffron */
}

.home-subtitle {
  font-size: 1.3rem;
  font-style: italic;
  color: #6d3b0c;
}

/* MESSAGE */
.home-message {
  max-width: 900px;
  margin: 40px auto;
  padding: 0 24px;
  font-size: 1.2rem;
  line-height: 1.9;
}

.home-message p {
  margin-bottom: 22px;
}

/* QUOTE */
.home-quote {
  text-align: center;
  margin: 60px auto;
  padding: 30px;
  background: rgba(255, 153, 51, 0.15);
  border-left: 6px solid #e65100;
  max-width: 700px;
}

.home-quote blockquote {
  font-size: 1.4rem;
  font-style: italic;
  margin-bottom: 10px;
}

.home-quote cite {
  font-size: 1rem;
  color: #7a3e00;
}

/* INVITATION */
.home-invite {
  max-width: 900px;
  margin: 60px auto 0;
  padding: 0 24px;
  text-align: center;
}

.home-invite h2 {
  font-size: 2rem;
  color: #bf360c;
  margin-bottom: 20px;
}

.home-invite p {
  font-size: 1.2rem;
  line-height: 1.8;
}

.home-blessing {
  margin-top: 30px;
  font-weight: bold;
  color: #5d2600;
}
